<template>
    <button @click="onClick()" :class="classstyle">{{ text }}</button>
</template>

<script>
    export default {
        name: 'TaskButton',
        props: {
            text: String,
            classstyle: String,
        },
        methods: {
            onClick() {
                alert("Clicked");
            }
        }
    }
</script>