<template>
  <div class="s-center max-w-screen-xl p-5">
    <MainHeader title="Task Tracker" />
    <TasksTweet @toggle-reminder="toggleReminder" @delete-task="deleteTask" :tasks="tasks" />
  </div>
</template>

<script>
import MainHeader from './components/MainHeader.vue'
import TasksTweet from './components/TaskTweets.vue'

export default {
  name: 'App',
  components: {
    MainHeader,
    TasksTweet
  },
  data() {
    return {
      tasks: [],
      showModal: false,
      title: '',
      message: ''
    }
  },
  methods: {
    deleteTask(id) {
      if(confirm('Are you sure?')){
        this.tasks = this.tasks.filter((task) => task.id !== id)
      }
    },
    toggleReminder(id) {
      this.tasks = this.tasks.map((task) => task.id === id ? {...task, reminder: !task.reminder} : task)
    },
  },
  created() {
    this.tasks = [
      {
        id: 1,
        text: 'Doctors Appointment',
        day: 'March 1st at 2:30pm',
        reminder: true
      },
      {
        id: 2,
        text: 'Meeting at School',
        day: 'March 2nd at 10:00am',
        reminder: true
      },
      {
        id: 3,
        text: 'Food Shopping',
        day: 'March 3rd at 1:00pm',
        reminder: false
      }
    ]
  }
}
</script>

<style>
  .s-center {
        margin: 0 auto;
        left: 0;
        right: 0;
    }
</style>
