<template>
    <div @dblclick="$emit('toggle-reminder', task.id)" :class="[task.reminder ? 'reminder' : '', 'task']" class="bg-slate-100 m-2 px-5 py-2">
        <h3 class="font-bold flex justify-between">
            {{ task.text }}
            <i @click="$emit('delete-task', task.id)" class="fas fa-times text-red-600 hover:text-red-300 cursor-pointer"></i>
        </h3>
        <p>{{ task.day }}</p>
    </div>
</template>

<script>
    export default {
        name: 'TaskTweet',
        props: {
            task: Object
        },
    }
</script>

<style>
    .reminder {
        border-left: 6px solid green;
    }
</style>